"use client";

import { useEffect, useState } from "react";

export function useGoldData() {
  const [tokens, setTokens] = useState<any[]>([]);
  const [analyses, setAnalyses] = useState<Record<string, any>>({});
  const [selectedTokenId, setSelectedTokenId] = useState<string | null>(null);

  const [goldPrice] = useState<number | null>(null);
  const [aggregateCap] = useState<number | null>(null);
  const [syncStatus] = useState<"ok" | "loading" | "error">("loading");
  const [lastUpdated, setLastUpdated] = useState<string | null>(null);

  useEffect(() => {
    let cancelled = false;

    async function load() {
      try {
        const res = await fetch("/api/gold-intel");
        const json = await res.json();

        if (cancelled) return;

        setTokens(Array.isArray(json.tokens) ? json.tokens : []);
        setAnalyses(json.analyses || {});
        setLastUpdated(json.generatedAt || null);

        if (json.tokens?.length && !selectedTokenId) {
          setSelectedTokenId(json.tokens[0].id);
        }
      } catch {
        if (!cancelled) {
          setTokens([]);
          setAnalyses({});
        }
      }
    }

    load();
    return () => {
      cancelled = true;
    };
  }, []);

  return {
    tokens,
    analyses,
    selectedTokenId,
    setSelectedTokenId,
    goldPrice,
    aggregateCap,
    lastUpdated,
    syncStatus,
  };
}
