// Client component to orchestrate data and render the dashboard.
'use client';

import {
  Header,
  TokenList,
  PerformanceChart,
  MarketIntel,
  AssetSpecs,
  RiskProfile,
} from "@/components/gold";
import { useGoldData } from "@/hooks/useGoldData";

export default function Page() {
  const {
    tokens,
    analyses,
    selectedTokenId,
    setSelectedTokenId,
    goldPrice,
    aggregateCap,
    lastUpdated,
    syncStatus,
  } = useGoldData();

  // Determine which token is selected (defaults to first token).
  const selected =
    (selectedTokenId && tokens.find((t: any) => t.id === selectedTokenId)) ||
    (tokens.length > 0 ? tokens[0] : null);

  // Look up the analysis for the selected token.
  const analysis = selected?.id ? analyses?.[selected.id] : null;

  // Convert lastUpdated string into a Date for Header and TokenList.
  const lastUpdatedDate: Date | null = lastUpdated ? new Date(lastUpdated) : null;

  // Derive a sync status for the header: show "syncing" while loading, then "synced".
  const headerSyncStatus = tokens.length === 0 ? ("syncing" as const) : ("synced" as const);

  return (
    <div className="flex flex-col h-screen">
      <Header
        goldPrice={goldPrice}
        aggregateCap={aggregateCap ?? 0}
        lastUpdated={lastUpdatedDate}
        syncStatus={headerSyncStatus}
      />

      <div className="flex flex-1 overflow-hidden space-x-6 p-6">
        {/* Left Panel - Top 10 Gold Tokens */}
        <div className="w-1/3 overflow-y-auto">
          <TokenList
            tokens={tokens}
            selectedId={selected ? selected.id : null}
            onSelect={(id: string) => setSelectedTokenId(id)}
            lastUpdated={lastUpdatedDate}
          />
        </div>

        {/* Right Panel - Charts and Grid Containers */}
        <div className="w-2/3 overflow-y-auto space-y-6">
          <PerformanceChart token={selected} />
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <RiskProfile token={selected} />
            <AssetSpecs token={selected} />
          </div>
          <div className="w-full">
            <MarketIntel analysis={analysis} />
          </div>
        </div>
      </div>
    </div>
  );
}
