// A risk profile component that displays risk metrics based on token data.
// Accepts a token (GoldToken | null) and displays relevant risk information.

'use client';

import type { GoldToken } from '@/types/gold';

export default function RiskProfile({ token }: { token: GoldToken | null }) {
  if (!token) {
    return (
      <div className="bg-card border border-border rounded p-6">
        <h3 className="text-[0.625rem] font-bold text-muted-foreground uppercase mb-4 tracking-wider">
          Risk Profile
        </h3>
        <p className="text-sm text-muted-foreground">
          Select a token to view risk profile.
        </p>
      </div>
    );
  }

  // Calculate risk indicators based on token data
  const hasPriceData = token.price && typeof token.price === 'number';
  const hasHighLowData = token.high !== null && token.low !== null;
  const hasVolumeData = token.volume !== null;
  const volatility = hasHighLowData && token.low ? Math.abs((token.high - token.low) / token.low) : null;

  // Determine risk level based on available data
  let riskLevel = "Unknown";
  if (volatility !== null) {
    if (volatility > 0.1) {
      riskLevel = "High";
    } else if (volatility > 0.05) {
      riskLevel = "Medium";
    } else {
      riskLevel = "Low";
    }
  }

  return (
    <div className="bg-card border border-border rounded p-6">
      <h3 className="text-[0.625rem] font-bold text-muted-foreground uppercase mb-4 tracking-wider">
        Risk Profile
      </h3>
      <div className="space-y-3">
        <div>
          <p className="text-xs text-muted-foreground">Risk Level</p>
          <p className={`text-sm font-medium ${riskLevel === 'High' ? 'text-red-500' : riskLevel === 'Medium' ? 'text-yellow-500' : 'text-green-500'}`}>
            {riskLevel}
          </p>
        </div>

        {volatility !== null && (
          <div>
            <p className="text-xs text-muted-foreground">Volatility (High/Low)</p>
            <p className="text-sm font-medium">{(volatility * 100).toFixed(2)}%</p>
          </div>
        )}

        <div>
          <p className="text-xs text-muted-foreground">Price Stability</p>
          <p className="text-sm font-medium">
            {token.chg !== null ? (Math.abs(token.chg) > 5 ? "Unstable" : "Stable") : "Unknown"}
          </p>
        </div>

        <div>
          <p className="text-xs text-muted-foreground">Market Activity</p>
          <p className="text-sm font-medium">
            {hasVolumeData ? (token.volume && token.volume > 1000000 ? "High" : "Low") : "Unknown"}
          </p>
        </div>
      </div>
    </div>
  );
}