export function MarketIntel({ analysis }: any) {
  const raw = typeof analysis?.summary === "string" ? analysis.summary.trim() : "";
  const cleaned = raw
    .replace(/[>*_`#-]/g, " ")
    .replace(/\r?\n+/g, " ")
    .replace(/\s+/g, " ")
    .trim();
  const sentences = cleaned.match(/[^.!?]+[.!?]?/g) || [];
  const text = sentences.map(s => s.trim()).filter(Boolean).slice(0, 5).join(" ") || null;
  return (
    <div className="bg-card border border-border rounded p-6">
      <h3 className="text-[0.625rem] font-bold text-muted-foreground uppercase mb-4 tracking-wider">
        Market Intel
      </h3>
      <p className="text-sm text-muted-foreground leading-relaxed min-h-[60px]">
        {text ? text : "Market analysis unavailable."}
      </p>
      {text && analysis?.provider && (
        <p className="text-[0.625rem] text-muted-foreground uppercase tracking-wide mt-1">
          Source: {analysis.provider}
        </p>
      )}
    </div>
  );
}
