"use client";

import { useMemo } from "react";
import { AreaChart, Area, XAxis, YAxis, Tooltip, ResponsiveContainer } from "recharts";

export function PerformanceChart({ token }: any) {
  const points = useMemo(() => {
    if (!Array.isArray(token?.prices7d) || token.prices7d.length < 2) return null;
    const valid = token.prices7d
      .filter((p: any) => Array.isArray(p) && typeof p[0] === "number" && typeof p[1] === "number")
      .map(([t, price]: [number, number]) => ({ t, price }))
      .sort((a, b) => a.t - b.t);
    return valid.length >= 2 ? valid : null;
  }, [token]);

  if (!points) {
    return (
      <div className="bg-card border border-border rounded p-6">
        <div className="h-[300px] flex items-center justify-center text-muted-foreground">
          7-day price history unavailable (CoinGecko free tier)
        </div>
      </div>
    );
  }

  const prices = points.map(p => p.price);
  const min = Math.min(...prices);
  const max = Math.max(...prices);
  const pad = (max - min) * 0.1 || 1;

  return (
    <div className="bg-card border border-border rounded p-6">
      <h3 className="text-[0.625rem] font-bold text-muted-foreground uppercase mb-4 tracking-wider">
        7-day price performance
      </h3>
      <div className="h-[300px]">
        <ResponsiveContainer width="100%" height="100%">
          <AreaChart data={points}>
            <defs>
              <linearGradient id="gold" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#c59b44" stopOpacity={0.8} />
                <stop offset="95%" stopColor="#c59b44" stopOpacity={0} />
              </linearGradient>
            </defs>
            <XAxis
              dataKey="t"
              type="number"
              scale="time"
              domain={["dataMin", "dataMax"]}
              tickFormatter={(v) =>
                new Date(v).toLocaleDateString("en-US", { month: "short", day: "numeric" })
              }
              axisLine={false}
              tickLine={false}
              tick={{ fontSize: 10 }}
              label={{ value: 'Date', position: 'insideBottom', offset: -5 }}
            />
            <YAxis
              domain={[min - pad, max + pad]}
              axisLine={false}
              tickLine={false}
              tickFormatter={(v) => `$${v.toFixed(0)}`}
              tick={{ fontSize: 10 }}
              label={{ value: 'Price (USD)', angle: -90, position: 'insideLeft', offset: 10 }}
            />
            <Tooltip
              formatter={(v: number) => [`$${v.toFixed(2)}`, "Price"]}
              labelFormatter={(v) =>
                new Date(v as number).toLocaleDateString("en-US", {
                  month: "short",
                  day: "numeric",
                })
              }
            />
            <Area type="monotone" dataKey="price" stroke="#c59b44" fillOpacity={1} fill="url(#gold)" />
          </AreaChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}
